.profile-info{
    display: flex;
    flex-direction: column;
    /*align-items: center;*/
    padding: 24px 24px;
    /*padding: 24px 0px;*/
}
.profile-pic{
    display: none;
    width: 64px;
    height: 64px;
    clip-path: circle(32px at center);
}
.profile-name{
    font-size:  var(--font-size-big);
    color: var(--white);
    padding: 10px 0px;
}
.profile-first{
    font-family: Raleway;
    font-weight: 700;
    padding: 0px 3px;
}
.profile-last{
    font-family: Raleway;
    font-weight: 700;
    /*font-weight: 300;*/
    /*font-style: italic;*/
    padding: 0px 3px;

}
.profile-position{
    color: var(--white2);
    /*font-style: italic;*/
    font-size:  var(--font-size-big);
}