.resume-container{
    display: flex;
    /*flex-direction: column;*/
    flex-direction: row;
    width: 100%;
    min-height: 100%;
    
}
::-webkit-scrollbar{
    width: 8px;
    margin: 5px;
}
::-webkit-scrollbar-track{
}
::-webkit-scrollbar-thumb{
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 4px;
    width: 4px;
}
.resume-info{
    display: flex;
    /*flex-direction: row;*/
    flex-direction: column;
    flex: 10;
    background-color: var(--pane-light);
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 24px;
}
.resume-details{
    display: flex;
    flex-direction: column;
    flex: 30;
    background-color: var(--pane-dark);
    padding-top: 16px;
    padding-bottom: 16px;
    padding-right: 24px;
}
.resume-profile{
    font-weight: 400;
    color: var(--white);
    padding: 10px 20px;
    text-align: justify;
    font-size: var(--font-size-text);
    line-height: 1.5;
}

@media only screen and (max-width: 600px) {
    .resume-container{
        flex-direction: column;
    }
}

.un-list{
    color: var(--white);
    font-size: var(--font-size-text);
    line-height: 24px;
}