.info-section{
    display: flex;
    flex-direction: column;
    padding: 0px 16px;
}   
.section-header{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.section-icon{
    height: 32px;
    width: 32px;
    margin: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    background-color: blue;
}
.section-icon svg{
    fill: var(--icon-color);
    height: 16px;
    width: 16px;
}
.section-title{
    font-family: GoogleSans;
    font-weight: 600;
    color:var(--white);
    font-size: var(--font-size-title);
    padding:0px 10px;
}
.section-content{
    display: flex;
    flex-direction: column;
}
