.ref-item{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px 10px;
    margin: 6px;
}
.circle{
    width: 8px;
    height: 8px;
    min-width: 8px;
    min-height: 8px;
    max-width: 8px;
    max-height: 8px;
    border-style: solid;
    border-radius: 8px;
    border-width: 2px;
    border-color: var(--red);
}
.circle.active{
    border-color: var(--green);
}
.ref-data{
    display: flex;
    flex-direction: column;
    padding: 8px;
    background: var(--pane-darker);
    border-radius: 4px;
    justify-content: center;
    width: 100%;
}

.ref-title{
    font-weight: 600;
    color: var(--green);
    font-size: var(--font-size-text-smaller);
    padding: 2px;
}
.ref-name{
    font-weight: 500;
    color: var(--white2);;
    font-size: var(--font-size-title);
    padding: 2px;
}
.ref-mail{
    font-weight: 400;
    color: var(--white2);;
    font-size: var(--font-size-text);
    padding: 2px;
    font-family: "Google Sans";
    display: flex;
    align-items: center;
}
.ref-phone{
    font-weight: 400;
    color: var(--white2);;
    font-size: var(--font-size-text);
    font-family: "Google Sans";
    padding: 2px;
    display: flex;
    align-items: center;
}
.ref-phone svg{
    width: 10px;
    height: 10px;
    padding: 0 4px;
    fill: var(--green);
}
.ref-mail svg{
    width: 10px;
    height: 10px;
    padding: 0 4px;
    fill: var(--green);
}
@media only screen and (max-width: 600px) {
    .research-xp{
        padding: 8px 8px;
    }
}