.research-xp{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px 10px;
}
.circle{
    width: 8px;
    height: 8px;
    min-width: 8px;
    min-height: 8px;
    max-width: 8px;
    max-height: 8px;
    border-style: solid;
    border-radius: 8px;
    border-width: 2px;
    border-color: var(--red);
}
.circle.active{
    border-color: var(--green);
}
.research-data{
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
    justify-content: center;
}
.research-title-year{
    display: flex;
    flex-direction: row;
    padding: 4px 0px;
    /* justify-content: center; */
    align-items: center;
}
.research-title{
    font-weight: 700;
    color: var(--white2);;
    font-size: var(--font-size-title);
}
.research-sep{
    font-weight: 700;
    color: var(--white2);;
    font-size: var(--font-size-title);
    padding: 0px 4px;
}
.research-duration{
    font-weight: 600;
    color: var(--red);
    font-size: var(--font-size-text);
}
.research-duration.active{
    color: var(--green);
}
.research-description{
    font-weight: 400;
    color: var(--white-darker);
    font-size: var(--font-size-text);
    padding: 4px 0px;
    justify-content: center;
}

@media only screen and (max-width: 600px) {
    .research-xp{
        padding: 8px 8px;
    }
}