@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{
  font-family: Raleway, sans-serif;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
}
body {
  background-color: var(--pane-dark);
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
	font-family: GoogleSans;
	font-style: normal;
	font-weight: bold;
	src: url(/static/media/GoogleSans-Bold.4457817a.ttf) format('truetype');
}
@font-face {
	font-family: GoogleSans;
	font-style: normal;
	font-weight: 500;
	src: url(/static/media/GoogleSans-Medium.8d57e401.ttf) format('truetype');
}
@font-face {
	font-family: GoogleSans;
	font-style: normal;
	font-weight: 300;
	src: url(/static/media/GoogleSans-Regular.b5c77a6a.ttf) format('truetype');
}
:root{
    /* Text Sizes */
    --font-size-big: 16px;
    --font-size-title: 12px;
    --font-size-text: 10px;
    --font-size-text-smaller: 8px;
    /* Theme Colors - Light*/
    --white: #ffffff;
    --white-darker: #eee;
    --white2: #FDD8EC;
    /*--white2: #fff;*/
    --pane-light: #24272e;
    --pane-dark: #1b1e24;
    --icon-color: #fff;
    --pane-darker: #121317;
    --green: #A2AD91;
    --red: #F2188F;
    /* Theme Colors - Dark*/
    /*--white: #000;*/
    /*--white-darker: #222;*/
    /*--white2: #FDD8EC;*/
    /*--white2: #000;
    --pane-light: #F8F8F8;
    --pane-dark: #F4F4F6;
    --pane-darker: #E8E9ED;
    --green: #7FBA7A;
    --icon-color: #fff;
    --red: #D40C7A;*/

}

.resume-container{
    display: flex;
    /*flex-direction: column;*/
    flex-direction: row;
    width: 100%;
    min-height: 100%;
    
}
::-webkit-scrollbar{
    width: 8px;
    margin: 5px;
}
::-webkit-scrollbar-track{
}
::-webkit-scrollbar-thumb{
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 4px;
    width: 4px;
}
.resume-info{
    display: flex;
    /*flex-direction: row;*/
    flex-direction: column;
    flex: 10 1;
    background-color: #24272e;
    background-color: var(--pane-light);
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 24px;
}
.resume-details{
    display: flex;
    flex-direction: column;
    flex: 30 1;
    background-color: #1b1e24;
    background-color: var(--pane-dark);
    padding-top: 16px;
    padding-bottom: 16px;
    padding-right: 24px;
}
.resume-profile{
    font-weight: 400;
    color: #ffffff;
    color: var(--white);
    padding: 10px 20px;
    text-align: justify;
    font-size: 10px;
    font-size: var(--font-size-text);
    line-height: 1.5;
}

@media only screen and (max-width: 600px) {
    .resume-container{
        flex-direction: column;
    }
}

.un-list{
    color: #ffffff;
    color: var(--white);
    font-size: 10px;
    font-size: var(--font-size-text);
    line-height: 24px;
}
.profile-info{
    display: flex;
    flex-direction: column;
    /*align-items: center;*/
    padding: 24px 24px;
    /*padding: 24px 0px;*/
}
.profile-pic{
    display: none;
    width: 64px;
    height: 64px;
    -webkit-clip-path: circle(32px at center);
            clip-path: circle(32px at center);
}
.profile-name{
    font-size:  var(--font-size-big);
    color: var(--white);
    padding: 10px 0px;
}
.profile-first{
    font-family: Raleway;
    font-weight: 700;
    padding: 0px 3px;
}
.profile-last{
    font-family: Raleway;
    font-weight: 700;
    /*font-weight: 300;*/
    /*font-style: italic;*/
    padding: 0px 3px;

}
.profile-position{
    color: var(--white2);
    /*font-style: italic;*/
    font-size:  var(--font-size-big);
}
.info-section{
    display: flex;
    flex-direction: column;
    padding: 0px 16px;
}   
.section-header{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.section-icon{
    height: 32px;
    width: 32px;
    margin: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    background-color: blue;
}
.section-icon svg{
    fill: var(--icon-color);
    height: 16px;
    width: 16px;
}
.section-title{
    font-family: GoogleSans;
    font-weight: 600;
    color:var(--white);
    font-size: var(--font-size-title);
    padding:0px 10px;
}
.section-content{
    display: flex;
    flex-direction: column;
}

.info-panel{
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
}   
.info-item{
    display: flex;
    cursor: pointer;
    flex-direction: row;
    padding: 5px 10px;
    align-items: center;
    justify-content: space-between;
}
.info-item2{
    display: flex;
    cursor: pointer;
    flex-direction: row;
    padding: 5px 10px;
    align-items: center;
    /*justify-content: space-between;*/
}
.info-item2 span{
    padding: 0px 4px;
}
.info-title{
    font-weight: 800;
    color:var(--white);
    font-size: var(--font-size-title);
}
.info-icon{
}
.info-icon svg{
    width: 12px;
    height: 12px;
    fill: var(--green);
}
.info-value{
    font-weight: 500;
    /*color:var(--white-darker);*/
    color:var(--white);
    font-family: GoogleSans;
    font-size: var(--font-size-text);
}
.pane{
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    margin: 10px 24px;
    background-color: var(--pane-darker);
    border-radius: 12px;
}   
.pane .section-icon{
    background-color: var(--green);
}

@media only screen and (max-width: 600px) {
    .pane{
        padding: 16px 8px;
        margin: 16px 12px;
    }   
}
.work-xp{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 2px 10px;
}
.circle{
    width: 8px;
    height: 8px;
    min-width: 8px;
    min-height: 8px;
    max-width: 8px;
    max-height: 8px;
    border-style: solid;
    border-radius: 8px;
    border-width: 2px;
    border-color: var(--red);
}
.circle.active{
    border-color: var(--green);
}
.work-data{
    display: flex;
    flex-direction: column;
    padding: 2px 10px;
    justify-content: center;
}
.work-title-year{
    display: flex;
    flex-direction: row;
    padding: 2px 0px;
    /* justify-content: center; */
    align-items: center;
}
.work-title{
    font-weight: 700;
    color: var(--white2);;
    font-size:  var(--font-size-title);
}
.work-sep{
    font-weight: 700;
    color: var(--white2);;
    font-size: 1 var(--font-size-title);
    padding: 0px 4px;
}
.work-duration{
    font-weight: 600;
    color: var(--red);
    font-size:  var(--font-size-text);
}
.work-tech{
    font-weight: 700;
    color: var(--white2);;
    font-size:  var(--font-size-text-smaller);
}
.work-duration.active{
    color: var(--green);
}
.work-description{
    font-weight: 400;
    color: var(--white-darker);
    font-size:  var(--font-size-text);
    padding: 2px 0px;
    justify-content: center;
}
.work-techs{
    font-weight: 400;
    color: var(--white-darker);
    font-size:  var(--font-size-text);
    padding: 2px 8px;
    justify-content: center;
}

@media only screen and (max-width: 600px) {
    .work-xp{
        padding: 8px 8px;
    }
}
