.info-panel{
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
}   
.info-item{
    display: flex;
    cursor: pointer;
    flex-direction: row;
    padding: 5px 10px;
    align-items: center;
    justify-content: space-between;
}
.info-item2{
    display: flex;
    cursor: pointer;
    flex-direction: row;
    padding: 5px 10px;
    align-items: center;
    /*justify-content: space-between;*/
}
.info-item2 span{
    padding: 0px 4px;
}
.info-title{
    font-weight: 800;
    color:var(--white);
    font-size: var(--font-size-title);
}
.info-icon{
}
.info-icon svg{
    width: 12px;
    height: 12px;
    fill: var(--green);
}
.info-value{
    font-weight: 500;
    /*color:var(--white-darker);*/
    color:var(--white);
    font-family: GoogleSans;
    font-size: var(--font-size-text);
}