.work-xp{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 2px 10px;
}
.circle{
    width: 8px;
    height: 8px;
    min-width: 8px;
    min-height: 8px;
    max-width: 8px;
    max-height: 8px;
    border-style: solid;
    border-radius: 8px;
    border-width: 2px;
    border-color: var(--red);
}
.circle.active{
    border-color: var(--green);
}
.work-data{
    display: flex;
    flex-direction: column;
    padding: 2px 10px;
    justify-content: center;
}
.work-title-year{
    display: flex;
    flex-direction: row;
    padding: 2px 0px;
    /* justify-content: center; */
    align-items: center;
}
.work-title{
    font-weight: 700;
    color: var(--white2);;
    font-size:  var(--font-size-title);
}
.work-sep{
    font-weight: 700;
    color: var(--white2);;
    font-size: 1 var(--font-size-title);
    padding: 0px 4px;
}
.work-duration{
    font-weight: 600;
    color: var(--red);
    font-size:  var(--font-size-text);
}
.work-tech{
    font-weight: 700;
    color: var(--white2);;
    font-size:  var(--font-size-text-smaller);
}
.work-duration.active{
    color: var(--green);
}
.work-description{
    font-weight: 400;
    color: var(--white-darker);
    font-size:  var(--font-size-text);
    padding: 2px 0px;
    justify-content: center;
}
.work-techs{
    font-weight: 400;
    color: var(--white-darker);
    font-size:  var(--font-size-text);
    padding: 2px 8px;
    justify-content: center;
}

@media only screen and (max-width: 600px) {
    .work-xp{
        padding: 8px 8px;
    }
}