.pane{
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    margin: 10px 24px;
    background-color: var(--pane-darker);
    border-radius: 12px;
}   
.pane .section-icon{
    background-color: var(--green);
}

@media only screen and (max-width: 600px) {
    .pane{
        padding: 16px 8px;
        margin: 16px 12px;
    }   
}