.edu-xp{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px 10px;
}
.line{
    width: 8px;
    height: 8px;
    min-width: 8px;
    min-height: 8px;
    max-width: 8px;
    max-height: 8px;
    border-radius: 2px;
    margin: 0px 3px;
    background-color: var(--red);
}
.line.active{
    background-color: var(--green);
}
.edu-data{
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
    justify-content: center;
}
.edu-title-year{
    display: flex;
    flex-direction: row;
    padding: 0px 0px;
    /* justify-content: center; */
    align-items: center;
}
.edu-title{
    font-weight: 700;
    color: var(--white2);;
    font-size: var(--font-size-title);
}
.edu-sep{
    font-weight: 700;
    color: var(--white2);;
    font-size: var(--font-size-title);
    padding: 0px 4px;
}
.edu-duration{
    font-weight: 600;
    color: var(--red);
    font-size: var(--font-size-text);
}
.edu-duration.active{
    color: var(--green);
}
.edu-where{
    font-weight: 400;
    color: var(--white-darker);
    font-size: var(--font-size-text);
    padding: 4px 0px;
    justify-content: center;
}
.edu-gpa-title{
    font-weight: 700;
    color: var(--white2);;
    font-size:  var(--font-size-text-smaller);
}
.edu-gpa{
    font-weight: 400;
    color: var(--white-darker);
    font-size: var(--font-size-text);
    padding: 4px 8px;
    justify-content: center;
}
@media only screen and (max-width: 600px) {
    .edu-xp{
        padding: 8px 8px;
    }
}